@import "../mixins";
._first-section{
  margin-top: -6.3125em;
}
.main{
  padding: 11.25em 0 3.75em;
  background-image: url(../img/main-bg.webp);
  background-size: cover;
  background-position: center;
  overflow: hidden;
  &__inner{
    display: flex;
  }
  &__left{
    max-width: 44em;
    padding-top: 1.25em;
    flex-shrink: 0;
  }
  .display1{
    @include grid100gap(.125em);
    .blue-text{
      padding-left: .59em;
    }
  }
  &__bottom{
    padding-left: 2.5em;
    margin-top: 1em;
    @include grid100gap(2.25em);
  }
  &__text{
    max-width: 25.25em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    &:not(:last-child){
      margin-right: .875em;
    }
  }
  &__right{
    flex-grow: 1;
    position: relative;
  }
  &__img{
    width: 130%;
    img{
      width: 100%;
      display: block;
    }
  }
  .video-btn{
    position: absolute;
    left: 11.625em;
    bottom: 3.8125em;
  }
}

.advantages{
  padding: 3.75em 0 4.375em;
  &__grid{
    display: flex;
    justify-content: space-between;
  }
}

.advantage-card{
  display: flex;
  max-width: 20.625em;
  &__icon{
    height: 2.5625em;
    flex-shrink: 0;
    margin-right: .9375em;
    img{
      width: auto;
      height: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.375em);
  }
}

.functions{
  padding: 3.75em 0 2.5em;
  overflow: hidden;
  &__top{
    max-width: 36em;
  }
  &__grid{
    display: grid;
    grid-template-columns: calc(100% - 4.625em - 21.3125em) 21.3125em;
    gap: 2.375em 4.625em;
    margin-top: 1.375em;
  }
  &__items{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 3.125em 5em;
    padding-top: 1.875em;
  }
  &__col{
    position: relative;
  }
}

.function-card{
  &__icon{
    width: 4em;
    height: 4em;
    @include center;
    background: #F3F3FF;
    border-radius: .8125em;
    margin-bottom: .6875em;
    img{
      max-width: 1.6875em;
    }
  }
}

.example{
  padding: 1.5em;
  background: #FFFFFF;
  box-shadow: 1.0625em .75em 3.375em rgba(62, 62, 101, 0.18);
  border-radius: .625em;
  position: sticky;
  top: 1em;
  max-width: 25em;
  &__bg{
    position: absolute;
    z-index: -1;
    top: -11.75em;
    width: 34.375em;
    display: block;
    left: -6.25em;
  }
  &__top{
    display: flex;
    justify-content: space-between;
  }
  &__info{
    @include grid100gap(.1875em);
    max-width: 9.625em;
  }
  &__btn{
    flex-shrink: 0;
    margin: .25em 0 0 1em;
    background: #28A464;
    color: #fff;
    border-radius: .5em;
    padding: 0 .75em;
    height: 2.1875em;
    img{
      margin-right: .5em !important;
    }
    &:hover{
      background: darken(#28A464,10%);
      color: #fff;
    }
  }
  &__company{
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 1.8125em;
    &-text{
      text-align: right;
      max-width: 6.6875em;
      @include grid100gap(.2em)
    }
    &-desc{
      color: #505050;
    }
  }
  &__logo{
    width: 6.25em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__for{
    margin-top: 1em;
    max-width: 11.25em;
  }
  &__sign{
    width: 100%;
    display: block;
    margin-top: 1.8125em;
  }
}

.signs{
  padding: 1.375em 2.5em 2.25em;
  background-color: $primary;
  color: #fff;
  margin-top: 4.875em;
  border-radius: .625em;
  &__title{
    font-family: $font2;
    font-weight: 400;
    text-align: center;
    font-size: 1.75em;
    line-height: 188%;
  }
  &__grid{
    display: grid;
    grid-template-columns: 1fr 1.2fr 1fr;
    gap: .8125em 3.75em;
    margin-top: 1.375em;
  }
}

.sign-card{
  display: flex;
  align-items: flex-start;
  &__icon{
    width: 2.5625em;
    flex-shrink: 0;
    margin-right: 1.125em;
    padding-top: .1875em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    @include grid100gap(.4375em);
  }
  &__title{
    font-weight: 600;
    font-size: 1em;
    line-height: 131%;
  }
  &__desc{
    font-size: .8125em;
    line-height: 154%;
  }
}

.secure{
  overflow: hidden;
  &__round{
    position: absolute;
    height: 100%;
    right: 3.125em;
    z-index: -1;
    pointer-events: none;
  }
  &__grid{
    padding: 5.875em 0 5.4375em;
    display: grid;
    grid-template-columns: 19.75em calc(100% - 19.75em - 4.875em);
    gap: 2.625em 4.875em;
    position: relative;
  }
  &__list{
    padding-left: 2.3125em;
    margin-top: 2em;
    @include grid100gap(2.5625em);
    li{
      display: flex;
      font-size: 1.0625em;
      line-height: 141%;
      &:before{
        content: '';
        display: block;
        flex-shrink: 0;
        margin-right: 1em;
        width: 1.235em;
        height: 1.53em;
        margin-top: -.17em;
        background-image: url(../img/shiels.svg);
        @include bgImgSize(100%);
      }
    }
  }
  &__right{
    padding-top: .875em;
    position: relative;
  }
  &__img{
    width: 114%;
    img{
      width: 100%;
      display: block;
    }
  }
  .video-btn{
    position: absolute;
    bottom: 5em;
    left: 8.5em;
  }
  &__hint{
    display: inline-block;
    position: relative;
    margin-top: 3.5em;
    padding-left: 4.6875em;
    &-img{
      position: absolute;
      width: 8.125em;
      display: block;
      left: 100%;
      top: 50%;
      transform: translateY(-50%);
      margin-left: .0625em;
    }
    &-img2{
      display: none;
    }
  }
}

.type{
  padding: 5.3125em 0;
  background-image: url(../img/type-bg.webp);
  background-size: cover;
  background-position: center;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,1fr);
    gap: 3.125em 5em;
  }
  &__title{
    max-width: 11.5em;
    grid-column: 1/3;
    align-self: center;
  }
}

.type-card{
  &__icon{
    width: 3.4375em;
    height: 3.4375em;
    border-radius: .8125em;
    background-color: #fff;
    @include center;
    img{
      max-width: 1.5625em;
      max-height: 1.5625em;
    }
  }
  &__content{
    margin-top: .6875em;
    @include grid100gap(.25em);
  }
  &__title{
    font-size: 1.25em;
    line-height: 135%;
    letter-spacing: -0.02em;
    font-weight: 500;
    font-family: $font2;
  }
  &__desc{
    font-size: .875em;
    line-height: 171%;
    color: $dark;
  }
}

.companies{
  padding: 6.25em 0;
  position: relative;
  &__top{
    max-width: 61.25em;
    margin: 0 auto;
    text-align: center;
  }
  &__slider{
    margin-top: 2em;
    position: relative;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
      overflow: hidden;
    }
    &-slide{
      padding: 1.25em 1.25em 0 0;
    }
  }
  .swiper-nav{
    display: none;
  }
}

.company-card{
  height: 5.0625em;
  border: 1px solid #F1F5FF;
  border-radius: .1875em;
  @include center;
  padding: 1em;
  //transition: all .3s;
  &:hover{
    border-color: $primary;
  }
  &__img{
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
}

.products{
  background-color: #D2DAEF;
  background-image: url(../img/products-bg.webp);
  background-size: cover;
  background-position: center;
  overflow: hidden;
}

.tariffs{
  padding: 5.375em 0 4.75em;
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__info{
    max-width: 35em;
    @include grid100gap(.5em);
  }
  &__desc{
    color: #40404A;
    line-height: 147%;
    max-width: 21.4em;
  }
  .swiper-nav{
    flex-shrink: 0;
    margin-left: 1em;
  }
  &__slider{
    margin-top: 2.75em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-wrapper{
      align-self: stretch;
    }
    &-slide{
      width: 25%;
      height: auto;
      padding-right: 1.25em;
      @media screen and (min-width: 577px){
        transition: all .3s;
        opacity: 0.5;
        &-active,
        &-next,
        &-next+.swiper-slide,
        &-next+.swiper-slide+.swiper-slide{
          opacity: 1;
        }
      }
    }
  }
}

.tariff-card{
  background-color: #FFFFFF;
  background-image: url(../img/tariff-bg.svg);
  background-size: 100% auto;
  background-position: left bottom;
  background-repeat: no-repeat;
  border-radius: 1.125em;
  padding: 2.1875em 1.875em 1.875em;
  height: 100%;
  display: flex;
  flex-direction: column;
  &__title{
    span{
      display: block;
      background: linear-gradient(270deg, #AF1CFB 58.57%, #FC50B7 100%), #191021;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      text-fill-color: transparent;
    }
  }
  &__desc{
    display: flex;
    margin-top: .75em;
    color: #625A69;
    &-img{
      width: 1.375em;
      flex-shrink: 0;
      margin-right: .75em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__bottom{
    padding-top: 3.6875em;
    margin-top: auto;
  }
  &__price{
    color: #fff;
    font-family: $font2;
    &-num{
      font-weight: 900;
      font-size: 2.125em;
      line-height: 159%;
    }
    &-text{
      font-weight: 600;
      font-size: 1em;
      line-height: 125%;
    }
  }
  &__btn{
    margin-top: .625em;
    padding: 0;
    width: 100%;
    height: 3.125em;
    border-radius: .25em;
    background-color: rgba(255, 255, 255, 0.09);
  }
}

.connect{
  display: flex;
  overflow: hidden;
  &__left{
    max-width: 35.5em;
    flex-shrink: 0;
    padding: 2.5em 0;
  }
  &__desc{
    margin-top: 1.11em;
    max-width: 25em;
    b,span{
      font-weight: 700;
    }
  }
  &__nav{
    margin-top: 1.6875em;
    display: flex;
  }
  &__right{
    margin-left: 11.125em;
    width: 21.5em;
    align-self: flex-end;
  }
  &__img{
    transform: translateY(1.375em);
    width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.contacts{
  color: #fff;
  position: relative;
  .map{
    position: absolute;
    right: 0;
    top: 0;
    height: 100%;
    width: 50%;
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 50%;
    height: 100%;
    background-image: url(../img/contacts-bg.webp);
    background-size: cover;
    background-position: center;
    pointer-events: none;
  }
  &__inner{
    padding: 3.125em 3.125em 3.125em 0;
    width: 50%;
    position: relative;
    z-index: 2;
  }
  &__top{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  &__title{
    font-weight: 900;
    text-transform: uppercase;
  }
  &__address{
    display: flex;
    max-width: 14.75em;
    padding-bottom: .5625em;
    &-img{
      padding-top: .25em;
      width: .6875em;
      flex-shrink: 0;
      margin-right: .5625em;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__mail{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-top: 1.875em;
    &-title{
      font-weight: 500;
      font-size: 1em;
      line-height: 138%;
      color: #9E9EF2;
    }
    &-link{
      margin-top: 1.0625em;
      display: flex;
      align-items: center;
      font-weight: 700;
      color: #DFDFFA;
      &:hover{
        color: #fff;
      }
      span{
        font-size: 1.25em;
        line-height: 110%;
        border-bottom: 1px solid rgba(#DFDFFA,.1);
      }
      img{
        width: 1.1875em;
        display: block;
        flex-shrink: 0;
        margin-right: .6875em;
      }
    }
  }
  &__socials{
    display: flex;
    margin-top: 4.375em;
    .social{
      background-color: #fff;
      opacity: .5;
      &:hover{
        opacity: 1;
      }
    }
  }
}



@media screen and (max-width: 992px){
  ._first-section{
    margin-top: -8.3125em;
  }

  .main{
    background-image: url(../img/main-bg2.webp);
    padding: 8.75em 0 1.5em;
    &__inner{
      display: block;
    }
    &__left{
      padding: 0;
      text-align: center;
    }
    .display1{
      .blue-text{
        padding: 0;
      }
    }
    &__bottom{
      padding: 0;
      margin-top: .875em;
      gap: .875em;
    }
    &__text{
      margin: 0 auto;
    }
    &__nav{
      justify-content: center;
    }
    &__img{
      width: 100%;
    }
  }

  .advantages{
    padding: 1.875em 0 1.5625em;
    &__grid{
      @include grid100gap(1.25em);
    }
  }

  .advantage-card{
    max-width: 100%;
    br{
      display: none;
    }
    &__content{
      gap: .25em;
    }
  }

  .functions{
    padding: 1.5625em 0;
    .display3{
      text-align: center;
    }
    &__grid{
      grid-template-columns: 100%;
      margin-top: 1.3125em;
    }
    &__items{
      padding: 0;
      gap: 1.5625em;
    }
  }

  .signs{
    &__title{
      line-height: 120%;
    }
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .sign-card{
    &__content{
      gap: .1875em;
    }
  }

  .secure{
    &__grid{
      grid-template-columns: 100%;
      padding: 1.5625em 0 4.375em;
    }
    &__round{
      display: none;
    }
    .display3{
      text-align: center;
    }
    &__list{
      margin-top: 1.75em;
      padding: 0 1.25em;
      gap: 1.5625em;
      li{
        font-size: 1em;
        line-height: 125%;
        &:before{
          width: 1.3125em;
          height: 1.5625em;
          margin-right: 1.125em;
        }
      }
    }
    &__hint{
      padding-left: 3.6875em;
      margin-top: 1.875em;
      &-img{
        display: none;
      }
      &-img2{
        display: block;
        position: absolute;
        left: 1.9375em;
        width: 2.625em;
        top: 1.5em;
      }
    }
    &__right{
      padding: 0;
    }
  }

  .type{
    padding: 2.3125em 0 2.75em;
    &__title{
      margin-bottom: .217em;
      grid-column: 1/-1;
      max-width: 100%;
      text-align: center;
    }
    &__grid{
      grid-template-columns: repeat(2,1fr);
      gap: 1.5625em 3em;
    }
  }

  .companies{
    padding: 2.5em 0 2.875em;
    &__slider{
      margin-top: .375em;
    }
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        padding: .625em .625em 0 0;
      }
    }
    .swiper-nav{
      display: flex;
      margin-top: 1.375em;
    }
  }

  .company-card{
    height: 4.0625em;
  }

  .tariffs {
    padding: 2.3125em 0 4.625em;
    position: relative;
    &__slider{
      margin: 0;
      padding: 1.5em 0 2.5em;
    }
    .swiper{
      &-slide{
        width: 50%;
      }
      &-nav{
        margin: 0;
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 1.625em;
      }
    }
  }

  .connect{
    padding: 1.625em 0 0;
    display: block;
    &__inner{
      display: block;
    }
    &__left{
      max-width: 100%;
      text-align: center;
      padding: 0 .625em;
    }
    &__desc{
      margin-top: .5em;
    }
    &__nav{
      margin-top: 1.125em;
      justify-content: center;
    }
    &__right{
      margin: 0 auto;
      max-width: 100%;
    }
  }

  .contacts{
    &:before{
      display: none;
    }
    .map{
      position: static;
      width: 100%;
      height: 21.875em;
    }
    .container{
      padding: 0;
    }
    &__inner{
      width: 100%;
      padding: 1.685em 3.125em 2.5em 3.125em;
      background-image: url(../img/contacts-bg2.webp);
      background-size: cover;
      background-position: center;
    }
    &__top{
      align-items: unset;
    }
    &__mail{
      margin-top: .875em;
    }
  }
}


@media screen and (max-width: 576px){
  .main{
    &__text{
      max-width: 15em;
    }
    &__nav{
      display: block;
      margin: 0 auto;
      width: 11.875em;
    }
    &__btn{
      padding: 0;
      width: 100%;
      &:not(:last-child){
        margin: 0 0 .875em;
      }
    }
    &__right{
      margin: .625em 0 0 0;
      width: calc(100% + 3.125em);
    }
    .video-btn{
      bottom: 1.5em;
      left: 3.0625em;
    }
  }

  .advantages{
    &__grid{
      padding: 0 .625em;
    }
  }

  .functions{
    &__items{
      grid-template-columns: 100%;
      padding: 0 .625em;
    }
  }

  .function-card{
    display: flex;
    align-items: center;
    &__icon{
      margin: 0 1.1875em 0 0;
      flex-shrink: 0;
    }
  }

  .example{
    padding: 1.125em 1.25em 1.25em;
    max-width: unset;
    &__bg{
      left: 0;
      top: -5.25em;
      width: calc(100% + 2.8125em);
    }
    &__btn{
      margin-top: .5625em;
    }
    &__sign,
    &__for{
      margin-top: 1.25em;
    }
  }

  .signs{
    margin-top: 1.5em;
    padding: 1.25em .8125em 1.875em 1.25em;
    &__title{
      font-size: 1.1875em;
    }
  }

  .sign-card{
    &__icon{
      margin-right: .9375em;
    }
    &__content{
      padding-top: .125em;
    }
    &__title{
      font-size: .9375em;
      line-height: 140%;
    }
    &__desc{
      font-size: .75em;
      line-height: 142%;
    }
  }

  .secure{
    .display3{
      span{
        display: block;
      }
    }
    &__img{
      width: 186%;
      margin-left: 1.5em;
    }
    .video-btn{
      left: 3.5625em;
      bottom: .5em;
    }
  }

  .type{
    background-image: url(../img/type-bg2.webp);
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .type-card{
    display: flex;
    &__icon{
      flex-shrink: 0;
    }
    &__content{
      margin: 0 0 0 .9375em;
    }
    &__title{
      font-size: 1em;
      line-height: 131%;
    }
    &__desc{
      font-size: .8125em;
      line-height: 138%;
    }
  }

  .products{
    background-image: url(../img/products-bg2.webp);
  }

  .tariffs{
    &__info{
      padding: 0 .625em;
      text-align: center;
    }
    &__slider{
      width: calc(100% + 1.25em);
      margin-left: -.625em;
      padding-left: .625em;
      padding-right: .625em;
      overflow: hidden;
    }
    .swiper{
      &-container{
        overflow: unset;
        width: 100%;
      }
      &-slide{
        width: 16.75em;
        padding-right: .625em;
      }
    }
  }

  .tariff-card{
    padding: 1.9375em 1.25em 1.5em;
    box-shadow: 0 1.8125em 2.0625em -1.1875em rgba(38, 36, 138, 0.43);
    &__desc{
      width: calc(100% + .5em);
    }
  }

  .connect{
    &__right{
      width: 17.5em;
      transform: translateX(1em);
    }
  }

  .contacts{
    &__inner{
      padding-left: .9375em;
      padding-right: .9375em;
    }
    &__top{
      display: block;
    }
    &__address{
      margin: 1.1875em 0 0;
      max-width: 13.125em;
      padding: 0;
      &-img{
        width: .625em;
        margin-right: .5em;
      }
    }
    &__mail{
      &-title{
        font-size: .8125em;
        line-height: 169%;
        padding-left: 1.615em;
      }
      &-link{
        margin-top: .1875em;
        img{
          width: .875em;
          margin-right: .4375em;
        }
        span{
          font-size: 1em;
          line-height: 138%;
        }
      }
    }
    &__socials{
      padding-left: 1.3125em;
      margin-top: 2.875em;
    }
  }
}
