.display1,
.display2,
.display3{
  letter-spacing: -0.02em;
  font-weight: 500;
  font-family: $font2;
}

.display1{
  font-size: 4.125em;
}

.display2{
  font-size: 2.9375em;
  line-height: 130%;
}

.display3{
  font-size: 2.6875em;
  line-height: 123%;
}

.display4{
  font-weight: 700;
  font-family: $font2;
  font-size: 1.4375em;
  line-height: 117%;
}

.display5{
  font-family: $font2;
  font-weight: 600;
  font-size: 1.3125em;
  line-height: 120%;
}

.text31{
  font-weight: 600;
  font-family: $font2;
  font-size: 1.9375em;
  line-height: 132%;
  letter-spacing: -0.02em;
}

.text18{
  font-size: 1.125em;
  line-height: 150%;
}

.text17{
  font-size: 1.0625em;
  line-height: 124%;
}

.text16{
  font-size: 1em;
  line-height: 150%;
}

.text15{
  font-size: .9375em;
  line-height: 173%;
}

.text14{
  font-size: .875em;
  line-height: 150%;
}

.text13{
  font-size: .8125em;
  line-height: 154%;
}

.text12{
  font-size: .75em;
  line-height: 158%;
}

.text11{
  font-size: .6875em;
  line-height: 182%;
}

.text10{
  font-size: .625em;
  line-height: 130%;
}

.blue-text{
  color: $primary;
}

.dark-text{
  color: $dark;
}

.black-text{
  color: $black;
}


@media screen and (max-width: 992px){
  .display1{
    font-size: 1.75em;
  }

  .display2{
    font-size: 1.6875em;
    line-height: 133%;
  }

  .display3{
    font-size: 1.4375em;
    line-height: 135%;
  }

  .display4{
    font-size: 1.3125em;
    line-height: 129%;
  }

  .display5{
    font-size: 1.0625em;
  }

  .text31{
    font-size: 1.375em;
    line-height: 145%;
  }


  .text18{
    font-size: .875em;
    line-height: 143%;
  }

  .text17,
  .text16{
    font-size: .875em;
    line-height: 129%;
  }

  .text14{
    font-size: .75em;
    line-height: 142%;
  }
}
