@import "../mixins";
@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    height: 3.75em;
    padding: 0 1.875em;
    border-radius: .3125em;
    font-weight: 600;
    &:focus {
        box-shadow: none;
    }
    span{
        font-size: .9375em;
    }
}

.m-btn-small{
    height: 2.625em;
    span{
        font-size: .8125em;
    }
    svg,
    img{
        width: .9375em;
        height: auto;
        flex-shrink: 0;
        &:nth-child(1){
            margin-right: 1em;
        }
    }
}

.m-btn-outline{
    border: 1px solid #D9E2F9;
    color: $black;
    &:hover{
        border-color: $black;
        color: $black;
        path{
            stroke: $primary;
        }
    }
}

.m-btn-black{
    background-color: $black;
    color: #fff;
    &:hover{
        background: #353C6C;
        color: #C1C5E3;
    }
}


.m-btn-primary{
    background-color: $primary;
    color: #fff;
    &:hover{
        background: #2E2CD1;
        color: #fff;
    }
}

.m-btn-white{
    background-color: #FFFFFF;
    color: $primary;
    &:hover{
        background: #2E2CD1;
        color: #fff;
    }
}

.m-btn-primary-outline{
    border: 1px solid $primary;
    color: $primary;
    &:hover{
        background: $primary;
        color: #fff;
    }
}

.video-btn{
    width: 14.375em;
    padding: .3125em;
    background: linear-gradient(180deg, #AF1CFB 0%, #FC50B7 100%);
    border-radius: 37.5em;
    color: #fff;
    display: flex;
    align-items: center;
    text-align: left;
    position: relative;
    &:before{
        content: '';
        position: absolute;
        width: calc(100% - 2em);
        height: calc(100% - 1.125em);
        border-radius: inherit;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        background: linear-gradient(180deg, #AF1CFB 0%, #FC50B7 100%);
        filter: blur(12.5px);
    }
    &__round,
    &__title{
        position: relative;
        z-index: 2;
    }
    &__round{
        border: .3125em solid rgba(0, 0, 0, 0.1);
        border-radius: 50%;
        background-color: #fff;
        width: 4.375em;
        height: 4.375em;
        margin-right: 1.0625em;
        flex-shrink: 0;
        @include center;
        svg{
            width: .9375em;
            height: auto;
        }
    }
    &__title{
        font-weight: 800;
        font-size: .8125em;
        line-height: 123%;
    }
    &:hover{
        color: #fff;
        background: linear-gradient(215.28deg, #AF1CFB 20.72%, #FC50B7 157.43%);
        .video-btn__round{
            path{
                fill: #F74DBB;
            }
        }
    }
}

.swiper-nav{
    display: flex;
    align-items: center;
    justify-content: center;
    .swiper-button{
        position: static;
        margin: 0 .3125em;
    }
}

.swiper-button{
    width: 3.75em;
    height: 3.75em;
    border-radius: 50%;
    @include center;
    border: 1px solid #EBEFF9;
    background-color: #fff;
    svg{
        width: .875em;
        height: auto;
    }
    &:hover{
        background: linear-gradient(180deg, #AF1CFB 0%, #FC50B7 100%);
        border: none;
        path{
            fill: #fff;
        }
    }
}

.social{
    width: 1.5em;
    height: 1.5em;
    border-radius: .25em;
    @include center;
    &:not(:last-child){
        margin-right: .625em;
    }
    svg,
    img{
        max-width: 1.125em;
        max-height: 1.125em;
    }
}


@media screen and (max-width: 576px){
    .m-btn{
        padding: 0 1.375em;
        height: 3em;
        span{
            font-size: .8125em;
        }
    }

    .video-btn{
        width: 11em;
        padding: .21875em;
        &__round{
            width: 3.0625em;
            height: 3.0625em;
            border-width: .21875em;
            margin-right: .75em;
            svg{
                width: .875em;
            }
        }
        &__title{
            font-size: .6875em;
        }
    }

    .swiper-nav{
        .swiper-button{
            margin: 0 .25em;
        }
    }

    .swiper-button{
        width: 3em;
        height: 3em;
        svg{
            width: .75em;
        }
    }
}
