.footer-menu{
  padding: 3.75em 0;
  background: #F8F9FA;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    width: 8em;
    display: block;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-nav{
  display: flex;
  align-items: center;
  &__link{
    font-weight: 500;
    color: #616174;
    &:not(:last-child){
      margin-right: 3em;
    }
    span{
      font-size: .875em;
    }
    &:hover{
      color: $black;
    }
  }
}

.footer-apps{
  width: 16.875em;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 1.25em;
  justify-content: flex-end;
  &__link{
    &:hover{
      img{
        transform: scale(1.1);
      }
    }
    img{
      width: 100%;
      display: block;
      transition: all .3s;
    }
  }
}

.footer-bottom{
  padding: 1.875em 0;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: flex-end;
  }
}

.footer{
  &__links{
    display: flex;
    align-items: center;
  }
  &__c{
    margin-right: auto;
  }
  &__by,
  &__link,
  &__c{
    font-size: .75em;
    line-height: 117%;
    color: #4B4B51;
  }
  &__link{
    margin-right: 3.33em;
  }
  &__by{
    margin: 0 auto;
  }
}

.lang{
  position: relative;
  &._active{
    .lang__dropdown{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    transition: all .3s;
    &:hover{
      color: $primary;
      cursor: pointer;
      path{
        fill: $primary;
      }
    }
    img{
      width: .9375em;
      display: block;
      flex-shrink: 0;
      margin-right: .375em;
    }
    span{
      font-size: .75em;
      line-height: 117%;
    }
    svg{
      width: .5em;
      height: auto;
      margin-left: .375em;
    }
  }
  &__dropdown{
    position: absolute;
    bottom: 100%;
    right: -1em;
    margin-bottom: .5em;
    padding: .75em 1.5em;
    background-color: #fff;
    box-shadow: 0 1.8125em 2.0625em -1.1875em rgba(38, 36, 138, 0.43);
    border-radius: .25em;
    transition: all .3s;
    opacity: 0;
    pointer-events: none;
  }
  &__item{
    display: flex;
    align-items: center;
    height: 2em;
    font-size: 1em;
    line-height: 150%;
    &:hover,
    &._active{
      color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .footer-menu{
    padding: 1.3125em 0 5.125em;
    &__inner{
      flex-direction: column;
    }
    &__logo{
      width: 6.625em;
    }
  }

  .footer-nav{
    display: block;
    column-count: 2;
    column-gap: 1.25em;
    width: 11.875em;
    margin: 1.3125em auto 0;
    &__link{
      width: 100%;
      display: inline-block;
      margin: 0 0 .9375em !important;
    }
  }

  .footer-apps{
    max-width: 100%;
    width: 16.5em;
    margin: .875em auto 0;
  }

  .footer-bottom{
    padding: 1.125em 0;
    position: relative;
    &__inner{
      padding-bottom: 2em;
    }
  }

  .footer{
    position: relative;
    &__links{
      position: absolute;
      bottom: 100%;
      left: 0;
      margin-bottom: .8125em;
      width: 100%;
      flex-direction: column;
    }
    &__link{
      margin: 0 0 1.16em;
    }
    &__c{
      max-width: 13.3em;
    }
    &__by{
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      padding: 1em;
    }
  }

  .lang{
    &__dropdown{
      right: 0;
    }
  }
}
