@import "../mixins";
.blog-item{
  position: relative;
  &__img{
    position: absolute;
    right: 1.25em;
    bottom: .5em;
    width: 20em;
    display: block;
  }
  &__inner{
    max-width: 46.5em;
    margin: 0 auto;
    padding: 8.875em 0;
    min-height: 29.25em;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
    position: relative;
    z-index: 2;
  }
}

.blog-item2{
  .blog-item__inner{
    max-width: 100%;
  }
}

.news{
  .container{
    max-width: 1080px;
  }
  &__media{
    position: relative;
  }
  &__img{
    margin-top: -13.875em;
    position: relative;
    z-index: 2;
    img{
      width: 100%;
      display: block;
    }
  }
  &__video{
    z-index: 2;
    margin-top: -8.375em;
  }
}

.video{
  position: relative;
  img{
    border-radius: .9375em;
    width: 100%;
    display: block;
  }
  .video-btn{
    @include centerAbsolute;
  }
}

.diploma{
  position: absolute;
  bottom: -2.875em;
  left: -3.75em;
  display: block;
  width: 11.625em;
  z-index: 2;
  &__img{
    width: 100%;
    img{
      width: 100%;
      display: block;
      box-shadow: 1.8125em 1.8125em 2.3125em -.6875em rgba(0, 0, 0, 0.21);
    }
  }
  .zoom{
    position: absolute;
    right: -.625em;
    bottom: -.5em;
  }
}

.zoom{
  width: 4em;
  height: 4em;
  border-radius: 50%;
  @include center;
  background: linear-gradient(180deg, #AF1CFB 0%, #FC50B7 100%);
  img{
    display: block;
    width: 1.0625em;
    height: auto;
    transition: inherit;
  }
  &:hover{
    background: $black;
    img{
      transform: scale(1.4);
    }
  }
}

.news-top{
  padding: 3.125em 0;
  background-color: $primary;
  color: #fff;
  &__inner{
    max-width: 26.875em;
    @include grid100gap(.6875em);
  }
}

.news-bottom{
  padding: 2em 0 2.875em;
  &__inner{
    display: grid;
    grid-template-columns: 30.625em calc(100% - 35.625em);
    gap: 5em;
  }
  &__text{
    @include grid100gap(1em);
    color: #545356;
    p{
      line-height: inherit;
    }
  }
}

.news-test{
  padding-bottom: 6em;
}

.news2{
  .container{
    max-width: 1180px;
  }
  .news-top__inner{
    max-width: 38.125em;
    .text15{
      max-width: 34em;
    }
  }
  .news-bottom__inner{
    grid-template-columns: 32.5em calc(100% - 33.75em);
    gap: 4em 1.25em;
  }
}

.reward{
  padding: 3.625em 0 5em;
  .container{
    max-width: 1050px;
  }
  &__grid{
    display: grid;
    grid-template-columns: auto 20.9375em;
    gap: 1.25em 2.8125em;
  }
  .display3{
    max-width: 11.6em;
  }
  &__desc{
    @include grid100gap(1em);
    margin-top: 1.2em;
    color: #545356;
    p{
      line-height: inherit;
    }
  }
  &__img{
    display: block;
    img{
      width: 100%;
      box-shadow: 1.8125em 1.8125em 2.3125em -.6875em rgba(0, 0, 0, 0.21);
      display: block;
    }
  }
}


@media screen and (max-width: 992px){
  .blog-item{
    &__inner{
      min-height: 20.5em;
      padding: 4em 0;
    }
  }

  .news{
    &__media{
      order: -1;
    }
    &__img{
      margin-top: -3.4375em;
    }
    &__video{
      margin-top: -3em;
      .video-btn{
        top: 100%;
      }
    }
  }

  .news-top{
    text-align: center;
    padding: 2.5em 0 5.75em;
    &__inner{
      margin: 0 auto;
      gap: .625em;
    }
  }

  .news-bottom{
    padding: 0;
    &__inner{
      grid-template-columns: 100%;
      gap: 3em;
    }
    .diploma{
      position: relative;
      margin: -3.375em auto 0;
      left: unset;
      bottom: unset;
    }
  }

  .news-test{
    padding: 3.4375em 0 2.5em;
  }

  .news2{
    .news-top__inner{
      .text15{
        margin-left: auto;
        margin-right: auto;
      }
    }
    .news-bottom__inner{
      grid-template-columns: 100%;
    }
  }

  .reward{
    padding: 0 0 5em;
    &__grid{
      grid-template-columns: 100%;
    }
    .display3{
      text-align: center;
    }
    &__desc{
      margin-top: 1.73em;
    }
    &__media{
      padding: 0 .875em;
    }
    &__img{
      max-width: 20em;
      margin: 0 auto;
    }
  }
}
