@import "../mixins";
.about-main{
  background-image: url(../img/about-bg.webp);
  padding: 10.625em 0 4.375em;
  &__inner{
    display: grid;
    grid-template-columns: 35.1875em calc(100% - 35.1875em);
    gap: .75em 0;
  }
  &__info{
    @include grid100gap(1.25em);
    padding-top: .3125em;
    position: relative;
    z-index: 2;
  }
  .display2{
    &>span{
      display: block;
      padding-left: .851em;
    }
  }
  &__text{
    padding-left: 2.857em;
    line-height: 186%;
    color: #363437;
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
  &__img{
    width: 121.4%;
    margin-left: -6.5em;
    img{
      display: block;
      width: 100%;
    }
  }
}

.about-company{
  padding: 5.3125em 0 5em;
  &__title{
    text-align: center;
    max-width: 12.84em;
    margin: 0 auto;
  }
  &__video{
    margin: 2.125em auto 0;
    width: 48.75em;
    max-width: 100%;
  }
}

.team{
  overflow: hidden;
  padding: 4em 0 4.375em;
  background-image: url(../img/team-bg.webp);
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    max-width: 17.3em;
  }
  &__slider{
    margin-top: 2.5em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1.25em);
    }
    &-slide{
      width: 25%;
      padding-right: 1.25em;
    }
  }
}

.team-card{
  &__img{
    display: block;
    @include imgCover(105%);
    img{
      border-radius: .6875em;
      display: block;
    }
  }
  &__content{
    margin-top: 1em;
    text-align: center;
    @include grid100gap(.75em);
  }
  &__name{
    font-weight: 500;
    font-family: $font2;
    font-size: 1.375em;
    line-height: 120%;
    letter-spacing: -0.02em;
  }
  &__prof{
    font-size: .75em;
    line-height: 143%;
    letter-spacing: -0.02em;
    color: #827C87;
  }
}

.opinion{
  padding: 4.375em 0 2.8125em;
  overflow: hidden;
  &__title{
    max-width: 13.95em;
  }
  &__slider{
    margin-top: 1.875em;
  }
  @media screen and (min-width: 993px){
    .swiper{
      &-nav{
        display: none;
      }
      &-container{
        width: calc(100% + 50em);
        margin-left: -25em;
      }
      &-wrapper{
        transform: unset !important;
        display: grid;
        grid-template-columns: 23.75em 30em 23.75em auto;
        grid-template-rows: 16.25em 16.25em;
        gap: 1.25em;
        grid-auto-flow: dense;
      }
      &-slide{
        width: 100% !important;
        &:nth-child(3){
          grid-row: 1/3;
          grid-column: 2/3;
        }
        &:nth-child(6){
          grid-row: 1/3;
          grid-column: 4/5;
        }
      }
    }
  }
}

.opinion-card{
  height: 100%;
  display: block;
  position: relative;
  &:hover{
    .zoom{
      opacity: 1;
    }
  }
  .zoom{
    opacity: 0;
    @include centerAbsolute;
    z-index: 4;
    transition: all .3s;
  }
  &__content{
    position: absolute;
    bottom: 0;
    left: 0;
    padding: 2.1875em;
    width: 100%;
    z-index: 3;
  }
  &__title{
    font-weight: 500;
    font-family: $font2;
    font-size: 1.1875em;
    line-height: 105%;
    letter-spacing: -0.02em;
    color: #fff;
  }
  &__img{
    @include imgCover(68.4%);
    padding: 0;
    height: 100%;
    img{
      display: block;
      border-radius: .625em;
    }
    &:before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      border-radius: .3125em;
      background: linear-gradient(180deg, rgba(52, 51, 67, 0) 28.08%, #242437 100%);
      z-index: 2;
    }
  }
}

.experience{
  padding: 2.8125em 0 3.4375em;
  overflow: hidden;
  &__title{
    max-width: 13.95em;
  }
  &__slider{
    margin-top: 1.75em;
  }
  .swiper{
    &-nav{
      display: none;
    }
    &-container{
      width: calc(100% + 3.375em);
    }
    &-slide{
      width: 25%;
      padding-right: 3.375em;
    }
  }
}

.experience-card{
  display: block;
  &__img{
    display: block;
    img{
      width: 100%;
      display: block;
      box-shadow: 1.8125em 1.8125em 2.3125em -.6875em rgba(0, 0, 0, 0.21);
    }
  }
}

.certificates{
  padding: 3.4375em 0 5.3125em;
  &__title{
    text-align: center;
    margin: 0 auto;
    max-width: 20em;
  }
  &__grid{
    margin-top: 2.5em;
    display: flex;
    justify-content: center;
    .swiper-slide{
      margin: 0 1.6875em;
      width: 15.875em;
    }
  }
}


@media screen and (max-width: 992px){
  .about-main{
    padding: 8.75em 0 1.375em;
    background-image: url(../img/about-bg2.webp);
    &__inner{
      grid-template-columns: 100%;
    }
    &__info{
      text-align: center;
      padding: 0 .625em;
      gap: .5em;
    }
    .display2{
      span{
        padding: 0;
      }
    }
    &__text{
      padding: 0;
    }
    &__img{
      margin: 0;
      width: calc(100% - .625em);
    }
  }

  .about-company{
    padding: 4.875em 0 3.125em;
    &__video{
      margin-top: .75em;
    }
  }

  .team{
    padding: 2.3125em 0 7.25em;
    position: relative;
    &__top{
      justify-content: center;
      text-align: center;
    }
    &__slider{
      margin-top: 1.5625em;
    }
    .swiper{
      &-slide{
        width: 14.5625em;
      }
      &-nav{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 2.8125em;
      }
    }
  }

  .team-card{
    &__content{
      gap: .25em;
    }
    &__name{
      font-size: 1.1875em;
    }
    &__prof{
      font-size: .8125em;
    }
  }

  .opinion{
    padding: 2.75em 0 1.25em;
    &__slider{
      margin: 1.25em 0 0;
    }
    .swiper{
      &-slide{
        width: 16.875em;
        padding-right: 1.25em;
      }
      &-nav{
        justify-content: center;
        margin-top: 2.5em;
      }
    }
  }

  .opinion-card{
    &__img{
      padding-bottom: 92%;
    }
    &__content{
      padding: .75em 1.25em;
    }
    &__title{
      font-size: 1em;
      line-height: 125%;
    }
  }

  .experience{
    padding: 1.25em 0 2.125em;
    .swiper{
      &-nav{
        display: flex;
        justify-content: center;
        margin-top: 2.5em;
      }
      &-container{
        width: calc(100% + 1.125em);
      }
      &-slide{
        width: 11.25em;
        padding-right: 1.125em;
      }
    }
  }

  .certificates{
    padding: 2.125em 0 3.25em;
    &__grid{
      margin-top: 1.25em;
      .swiper-slide{
        margin: 0 .625em;
        width: 10em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .certificates{
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,1fr);
      gap: 1.25em;
      .swiper-slide{
        margin: 0;
        width: 100%;
      }
    }
  }
}
