.search-field{
  margin: 0;
  position: relative;
  width: 28.125em;
  max-width: 100%;
  &__btn{
    position: absolute;
    right: 1.25em;
    top: 50%;
    transform: translateY(-50%);
    svg{
      width: 1.0625em;
      height: auto;
    }
    &:hover{
      border-color: $primary;
      path{
        stroke: $primary;
      }
    }
  }
  input{
    width: 100%;
    background: #F5F6FA;
    border-radius: 43.75em;
    padding: 0 3em 0 2em;
    height: 4.16em;
    font-size: .75em;
    border: none;
    &::placeholder{
      color: #A2A8BF;
    }
    &:focus{
      box-shadow: 0 0 .5em .125em rgba($primary,.4);
    }
  }
}


@media screen and (max-width: 992px){
  .search-field{
    &__btn{
      right: .875em;
      svg{
        width: .8125em;
      }
    }
    input{
      height: 3.33em;
      padding: 0 1.33em;
    }
  }
}

.fg {
  position: relative;
  margin: 0;
  input,
  label{
    font-size: 1.0625em;
    color: #46474A;
  }
  input {
    border: none;
    border-radius: .29em;
    background: #F5F6FA;
    padding: 1.588em 1.76em 0 1.76em;
    height: 3.88em;
    width: 100%;
    &.input-active,
    &:focus {
      &+label{
        font-size: .75em;
        line-height: 217%;
        top: .5em;
        left: 2.5em;
      }
    }
  }
  label{
    position: absolute;
    left: 1.76em;
    top: 1.176em;
    line-height: 153%;
    color: #A2A8BF;
    transition: all .3s;
  }
}

.check {
  position: relative;
  input {
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
    &:checked+label {
      &::before {
        // background-color: $green;
        border: 3px solid #fff;
      }
    }
    &:disabled+label {
      color: #AAAAAA;
      pointer-events: none;
    }
  }
  label {
    padding-left: 40px;
    position: relative;
    min-height: 20px;
    font-size: 14px;
    line-height: 20px;
    cursor: pointer;
    &:before {
      content: '';
      position: absolute;
      z-index: 1;
      border-radius: 2px;
      background: rgba(255, 255, 255, 0.01);
      border: 1px solid rgba(255, 255, 255, 0.45);
      width: 20px;
      height: 20px;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      transition: all .100s;
    }
  }
}
