@import "../mixins";
.faq-main{
  background-image: url(../img/faq-bg.webp);
  padding: 10.9375em 0 6.25em;
  .display2{
    span{
      padding: 0;
    }
  }
  .about-main{
    &__inner{
      grid-template-columns: 100%;
      gap: 2.125em;
    }
    &__left{
      max-width: 29.75em;
    }
    &__right{
      padding-left: 2.5em;
    }
  }
  .search-field{
    max-width: 49.0625em;
    width: 100%;
    input{
      background-color: #fff;
      @media screen and (min-width: 993px){
        padding-left: 1.71em;
        font-size: .875em;
        height: 4.71em;
      }
    }
  }
}

.guide{
  padding: 3.75em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(4,calc(25% - .9375em));
    gap: 1.25em;
  }
}

.guide-card{
  background: #FFFFFF;
  border: 1px solid #F3F3F3;
  box-shadow: 0 .75em 1.625em rgba(0, 0, 0, 0.06);
  border-radius: 9px;
  padding: 2.5em 2em 2em;
  display: flex;
  flex-direction: column;
  &__icon{
    width: 1.4375em;
    height: 1.4375em;
    display: flex;
    align-items: center;
    img{
      max-width: 100%;
      max-height: 100%;
      display: block;
    }
  }
  &__info{
    margin-top: 1.75em;
    @include grid100gap(.8125em);
  }
  &__title{
    font-weight: 500;
    font-family: $font2;
    font-size: 1.375em;
    line-height: 123%;
  }
  &__desc{
    color: #6F6975;
    line-height: 154%;
  }
  &__nav{
    margin-top: auto;
    padding-top: 1.6875em;
    display: flex;
  }
  &__link{
    color: $primary;
    font-weight: 600;
    font-size: .875em;
    line-height: 120%;
    border-bottom: 1px solid rgba($primary,.3);
    &:hover{
      border-color: $primary;
    }
  }
}

.faq{
  padding: 4.375em 0;
  background-image: url(../img/faq-bg3.webp);
  &__title{
    max-width: 10em;
  }
  &__items{
    margin-top: 1.9375em;
    padding-left: 7.5em;
    @include grid100gap(.625em);
  }
}

.qa-card{
  &._active{
    .qa-card__show{
      border-color: $primary;
      color: $primary;
    }
    .qa-card__btn{
      transform: rotate(-45deg);
    }
  }
  &__show{
    cursor: pointer;
    padding: .5em .5em .5em 1.875em;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: #4D4951;
    background: #FFFFFF;
    border: 1px solid #fff;
    box-shadow: 0 .4375em .75em rgba(0, 0, 0, 0.02);
    border-radius: 37.5em;
    min-height: 3.75em;
  }
  &__title{
    font-weight: 600;
  }
  &__btn{
    flex-shrink: 0;
    margin-left: 1.5em;
    width: 2.75em;
    height: 2.75em;
    border-radius: 50%;
    @include center;
    svg{
      width: 1em;
      height: auto;
    }
    &:hover{
      background-color: $primary;
      path{
        fill: #fff;
      }
    }
  }
  &__hidden{
    padding: 1.25em 1.875em 1em;
    display: none;
  }
  &__text{
    max-width: 55.33em;
    @include grid100gap(1em);
    p{
      line-height: inherit;
    }
  }
}

.answers{
  padding: 4.625em 0;
  overflow: hidden;
  &__grid{
    display: grid;
    grid-template-columns: 23.75em calc(100% - 29.5em);
    gap: 5.75em;
  }
  &__tabs{
    position: sticky;
    top: 1em;
    background: #FFFFFF;
    border: 1px solid #F3F3F3;
    box-shadow: 0 .75em 1.625em rgba(0, 0, 0, 0.06);
    border-radius: .5625em;
    overflow: hidden;
  }
}

.tab{
  min-height: 3.75em;
  padding: 1.125em 2em;
  display: flex;
  align-items: center;
  font-weight: 500;
  transition: all .3s;
  span{
    font-size: 1em;
    line-height: 150%;
  }
  svg{
    width: 1.4375em;
    height: auto;
    margin-right: 1.1875em;
  }
  &._active,
  &:hover{
    background-color: $primary;
    color: #fff;
    cursor: pointer;
    path{
      fill: #D0CFF4 ;
    }
  }
}

.tab-content{
  display: none;
  &._active{
    display: block;
  }
}

.text{
  @include grid100gap(1.5em);
  h2{
    font-weight: 500;
    font-family: $font2;
    font-size: 2.125em;
    line-height: 120%;
    margin-left: -1.47em;
  }
  li,
  p{
    font-size: .9375em;
    line-height: 160%;
    color: #3D3841;
  }
  ol{
    counter-reset: list;
    margin-bottom: 3em;
    li{
      counter-increment: list;
      position: relative;
      &:before{
        content: '0'counter(list) '.';
        font-size: .733em;
        line-height: 218%;
        position: absolute;
        top: 0;
        right: 100%;
        margin-right: .63em;
        color: rgba(0, 0, 0, 0.23);
      }
    }
  }
  b{
    font-weight: 800;
  }
  mark{
    display: inline-flex;
    align-items: center;
    padding: 0 .4em;
    border: 1px dashed $primary;
    border-radius: .2em;
    background-color: transparent;
    font-weight: 800;
    color: $primary;
    margin-bottom: .2em;
  }
}

.screens{
  margin-top: 3.125em;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: 1.5625em;
    font-weight: 600;
    font-family: $font2;
  }
  &__slider{
    margin-top: 1.5em;
  }
  .swiper{
    &-container{
      width: calc(100% + 1em);
    }
    &-slide{
      width: 50%;
      padding-right: 1em;
      opacity: 0;
      transition: all .3s;
      &-active,
      &-next{
        opacity: 1;
      }
      &-next~.swiper-slide{
        opacity: 0.3;
      }
    }
  }
}

.screen-img{
  display: block;
  @include imgCover(51.88%);
  img{
    display: block;
    border-radius: .5625em;
  }
}

.problems{
  margin-top: 4em;
  padding: 1.5625em 2.125em 2.5em;
  background: #F3F6FD;
  border-radius: .5625em;
  @include grid100gap(1.75em);
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
  &__link{
    font-size: 1.0625em;
    line-height: 120%;
    text-decoration: underline;
    text-decoration-color: rgba($primary,.3);
    color: $primary;
    &:not(:last-child){
      margin-bottom: .5em;
    }
  }
}


@media screen and (max-width: 992px){
  .faq-main{
    background-image: url(../img/faq-bg2.webp);
    padding: 8.75em 0 3.75em;
    .about-main{
      &__inner{
        gap: 2.875em;
      }
      &__left{
        max-width: 100%;
      }
      &__right{
        padding-left: 0;
      }
      &__info{
        gap: .75em;
      }
      &__text{
        font-size: .875em;
      }
    }
    .search-field{
      input{
        height: 4.166em;
        padding-left: 1.66em;
      }
    }
  }

  .guide{
    padding: 2.1875em 0 3.3125em;
    &__grid{
      display: grid;
      grid-template-columns: repeat(2,calc(50% - .5em));
      gap: .9375em 1em;
    }
  }

  .faq{
    padding: 2.3125em 0 3.125em;
    background-image: url(../img/faq-bg4.webp);
    background-position: left bottom;
    &__title{
      margin: 0 auto;
      text-align: center;
    }
    &__items{
      gap: .9375em;
      margin-top: 1.125em;
      padding-left: 0;
    }
  }

  .qa-card{
    &__show{
      padding: .75em .8125em .75em 1.875em;
    }
    &__btn{
      width: 2.125em;
      height: 2.125em;
      margin-left: .5em;
      svg{
        width: .75em;
      }
    }
    &__hidden{
      padding: 1.25em 0 0 1.875em;
    }
    &__text{
      font-size: .875em;
    }
  }

  .answers{
    padding: 2.8125em 0 3.125em;
    &__grid{
      grid-template-columns: 100%;
      gap: 2.5em;
    }
  }

  .text{
    gap: 1.25em;
    h2{
      font-size: 1.4375em;
      line-height: 135%;
      margin: 0;
    }
    ol{
      gap: 1.25em;
      margin-bottom: 1em;
      padding-left: 1.25em;
    }
    li{
      &:before{
        font-size: .84em;
      }
    }
    li,
    p{
      font-size: .8125em;
      line-height: 154%;
    }
  }

  .screens{
    padding-bottom: 4.375em;
    margin-top: 2.8125em;
    position: relative;
    &__top{
      justify-content: center;
    }
    &__title{
      font-size: 1.125em;
      line-height: 133%;
    }
    &__slider{
      margin-top: 1.125em;
    }
    .swiper{
      &-container{
        width: calc(100% + .625em);
      }
      &-slide{
        padding-right: .625em;
      }
      &-nav{
        position: absolute;
        left: 50%;
        transform: translateX(-50%);
        bottom: 0;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .guide{
    &__grid{
      grid-template-columns: 100%;
    }
  }

  .guide-card{
    &__info{
      margin-top: .8125em;
    }
    &__desc{
      font-size: .8125em;
    }
  }

  .tab{
    background-image: url(../img/tab-arrow.svg);
    background-repeat: no-repeat;
    background-size: .3125em auto;
    background-position: right 1.25em center;
    padding: .5em 2em .5em 1.25em;
    &._active{
      background-image: url(../img/tab-arrow2.svg);
      background-size: auto .3125em;
    }
    span{
      font-size: .875em;
    }
    svg{
      width: 1.125em;
      margin-right: 1.5em;
    }
  }

  .screens{
    .swiper-slide{
      width: 15.25em;
      opacity: 1;
    }
  }

  .problems{
    margin-top: 2.625em;
    padding: 1.25em 1.5em;
    gap: .5em;
    .screens__title{
      max-width: 90%;
    }
    &__link{
      font-size: .875em;
      line-height: 143%;
      &:not(:last-child){
        margin-bottom: 1.5em;
      }
    }
  }
}
