@import 'vars';
html {
    width: 100%;
    font-size: 1280px;
    @media screen and (max-width: 1280px){
        font-size: 100vw;
    }
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

.page {
    width: calc(100vw - (100vw - 100%));
    overflow-x: hidden;
    font-family: $font1;
    font-weight: 400;
    color: $black;
    font-size: 1.25%;
    &.fixed-body {
        //overflow-y: hidden;
        position: fixed;
        overflow-y:scroll
    }
}

.container {
    max-width: 1280px;
    width: 100%;
    margin: 0 auto;
    padding: 0 3.125em;
}

a {
    &:hover {
        color: $primary;
    }
}


@media screen and (max-width: 992px){
    .page{
        font-size: 2.2%;
    }
}


@media screen and (max-width: 576px){
    .page{
        font-size: 5%;
    }

    .container{
        padding: 0 .625em;
    }
}
