@import "../mixins";
.call-modal,
.menu{
  opacity: 0;
  pointer-events: none;
  transition: opacity .6s, transform .4s;
  transform: translateY(-200%);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  background-color: #fff;
  background-image: url(../img/menu-bg.svg);
  background-size: 24.5em auto;
  background-position: center bottom;
  background-repeat: no-repeat;
}

.menu{
  z-index: 100;
  @media screen and (min-width: 993px){
    .container{
      padding-left: 11.25em;
    }
  }
  &__logo{
    width: 3.125em;
    flex-shrink: 0;
    display: block;
    margin-right: 3.4375em;
    img{
      width: 100%;
      display: block;
    }
  }
  .header-action{
    margin-left: auto;
  }
  &__close{
    position: absolute;
    left: 3em;
    top: 50%;
    transform: translateY(-50%);
  }
  &__links{
    display: flex;
    align-items: flex-start;
    flex-direction: column;
  }
  &__link{
    font-family: $font2;
    font-weight: 500;
    display: flex;
    align-items: center;
    &:not(:last-child){
      margin-bottom: 2.8125em;
    }
    &:before{
      content: '';
      flex-shrink: 0;
      width: 0;
      height: 1.5em;
      background-image: url(../img/arrow.svg);
      @include bgImgSize(1.25em);
      background-position: left center;
      transition: inherit;
    }
    span{
      font-size: 1.8125em;
      line-height: 103%;
      letter-spacing: -0.02em;
    }
    &._active{
      color: $primary;
      &:before{
        width: 2.1875em;
      }
    }
  }
  &__right{
    display: flex;
  }
  &__c{
    margin-left: 7.5em;
    font-size: .75em;
    line-height: 117%;
    leading-trim: both;
    text-edge: cap;
    color: #4B4B51;
  }
  .header-contacts__social{
    background: #F5F6FA;
  }
}

._close-btn{
  width: 2.8125em;
  height: 2.8125em;
  border-radius: 50%;
  border: 1px solid #F5F6FA;
  svg{
    width: .8125em;
    height: auto;
  }
  &:hover{
    border-color: $primary;
    path{
      stroke: $primary;
    }
  }
}

.menu-top{
  padding: 1.25em 0;
  border-bottom: 1px solid #F5F6FA;
  position: relative;
  &__inner{
    display: flex;
    align-items: center;
  }
}

.menu-content{
  padding: 3.625em 0 4em;
  &__inner{
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
}

.call-modal{
  z-index: 101;
  padding: 5em;
  transform: unset;
  &._active{
    opacity: 1;
    pointer-events: unset;
  }
  &__close{
    position: absolute;
    top: 3.125em;
    right: 3.125em;
  }
  &__inner{
    max-width: 27.5em;
    width: 100%;
    margin: 0 auto;
  }
  .display2{
    text-align: center;
  }
  &__form{
    margin: 1.75em auto 0;
    width: 24.5em;
    max-width: 100%;
    @include grid100gap(1.25em);
  }
  &__hint{
    margin-top: .625em;
    display: flex;
    font-weight: 500;
    color: #989BA7;
    &-icon{
      width: 1.1875em;
      flex-shrink: 0;
      margin-right: .9375em;
      img{
        display: block;
        width: 100%;
      }
    }
    .text12{
      letter-spacing: -0.02em;
    }
  }
}


@media screen and (max-width: 992px){
  .menu{
    background-image: unset;
    &__logo{
      display: none;
      transform: unset;
    }
    &__close{
      position: static;
      transform: unset;
    }
    .header-action__login{
      background-color: transparent;
      box-shadow: none;
      position: static;
      padding: 0 0 0 1.625em;
      width: auto;
      background-position: left center;
      color: #656D81;
      height: unset;
      min-height: 2em;
      span{
        display: block;
      }
    }
    .search-field{
      width: 100%;
      margin-top: 1.0625em;
      order: 2;
    }
    &__link{
      &:before{
        background-size: .75em auto;
      }
      span{
        font-size: 1.0625em;
        line-height: 176%;
      }
      &:not(:last-child){
        margin-bottom: .5625em;
      }
      &._active{
        &:before{
          width: 1.25em;
        }
      }
    }
    &__right{
      flex-direction: column;
      padding-top: 3em;
    }
    .header-contacts{
      position: static;
      transform: unset;
    }
    &__c{
      margin: 2.42em 0 0;
      text-align: center;
    }
  }

  ._close-btn{
    width: 1.875em;
    height: 1.875em;
    svg{
      width: .625em;
    }
  }

  .menu-top{
    padding: .8125em 0;
    border: none;
    &__inner{
      flex-wrap: wrap;
      justify-content: space-between;
    }
  }

  .menu-content{
    padding: .625em 0 4em;
    &__inner{
      display: block;
      padding: 0 2.125em;
    }
  }

  .call-modal{
    padding: 3em .625em 5em;
    &__close{
      top: .5em;
      right: .5em;
    }
    &__form{
      margin-top: 1em;
      gap: .5em;
    }
  }
}
