@import "../mixins";
.bg-cover{
  background-size: cover;
  background-position: center;
}

.blog-main{
  padding: 11em 0 4.375em;
  background-image: url(../img/blog-bg.webp);
  &__inner{
    max-width: 31em;
  }
  &__info{
    @include grid100gap(.875em);
    .text18{
      margin-left: 2.38em;
    }
  }
  .socials{
    margin-top: 5.1875em;
    display: flex;
  }
}

.socials{
  .social{
    background: #151726;
    opacity: 1;
    &:not(:hover):not(.ig){
      path{
        fill: #fff;
      }
    }
    &:not(:hover).ig{
      stop{
        stop-color: #fff;
      }
    }
    &:hover{
      background: #fff;
    }
  }
}

.blog{
  padding: 4.6875em 0 5em;
  &__grid{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2.5em / 3));
    gap: 3.125em 1.25em;
    grid-auto-flow: dense;
  }
  .test{
    grid-column: 1/-1;
  }
}

.blog-card{
  display: block;
  position: relative;
  &._big{
    grid-column: span 2;
    .blog-card{
      &__img{
        &:before{
          content: '';
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          border-radius: .3125em;
          background: linear-gradient(180deg, rgba(21, 23, 38, 0.26) 0%, rgba(21, 23, 38, 0.89) 100%, rgba(21, 23, 38, 0.63) 100%);
          z-index: 2;
        }
      }
      &__content{
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 1.5em 2.5em;
        max-width: 28.75em;
        width: 100%;
        z-index: 3;
        color: #FFFFFF;
      }
      &__desc{
        color: #FFFFFF;
      }
    }
  }
  &__img{
    display: block;
    @include imgCover(66.8%);
    img{
      border-radius: .3125em;
    }
  }
  &__content{
    padding: 1.25em .625em 0;
    @include grid100gap(.4375em);
  }
  &__title{
    font-weight: 600;
    font-family: $font2;
    font-size: 1.1875em;
    line-height: 147%;
  }
  &__desc{
    font-size: .8125em;
    line-height: 146%;
    color: #4D4B50;
  }
  &__date{
    font-size: .6875em;
    line-height: 173%;
  }
}

.test{
  padding-top: 6.25em;
  overflow: hidden;
  &__inner{
    color: #fff;
    background-image: url(../img/test-bg.webp);
    padding: 2.5em 4.375em;
    display: grid;
    grid-template-columns: calc(100% - 39em) 35em;
    gap: 1.75em 4em;
    border-radius: .625em;
  }
  &__content{
    @include grid100gap(1.25em);
  }
  .text18{
    max-width: 24.72em;
  }
  &__nav{
    display: flex;
  }
  &__btn{
    padding: 0 5em;
    height: 3.75em;
  }
  &__img{
    position: relative;
    height: 100%;
    img{
      height: calc(100% + 11.75em);
      position: absolute;
      bottom: -4em;
      right: -3em;
    }
  }
}

.pagination{
  margin-top: 6.5em;
  display: flex;
  align-items: center;
  justify-content: center;
  &__nums{
    display: flex;
    align-items: center;
    margin: 0 .625em;
  }
  &__btn,
  &__num{
    width: 3.125em;
    height: 3.125em;
    border-radius: 50%;
    border: 1px solid #EBF1F2;
    @include center;
    margin: 0 .5em;
    span{
      font-size: .875em;
    }
    &._active{
      pointer-events: none;
    }
    &:hover,
    &._active{
      background-color: $primary;
      border-color: $primary;
      color: #fff;
    }
  }
  &__btn{
    @media screen and (min-width: 993px){
      width: auto;
      height: auto;
      border: none;
      svg{
        width: 1.625em;
        height: auto;
        margin: 0 .625em;
      }
      &:hover{
        color: $primary;
        background-color: transparent;
        path{
          fill: $primary;
        }
      }
    }

  }
}


@media screen and (max-width: 992px){
  .blog-main{
    padding: 11.25em 0 5em;
    background-image: url(../img/blog-bg2.webp);
    &__inner{
      text-align: center;
      margin: 0 auto;
    }
    &__info{
      gap: 1em;
      .text18{
        margin: 0;
      }
    }
    .socials{
      margin-top: 4.625em;
      justify-content: center;
    }
  }

  .blog{
    padding: 2.5em 0 3.125em;
    &__grid{
      grid-template-columns: repeat(2,calc(50% - .625em));
    }
  }

  .blog-card{
    &._big{
      grid-column: 1/-1;
      .blog-card{
        &__img{
          padding-bottom: 110.6%;
        }
        &__content{
          padding: 1.25em;
        }
      }
    }
    &__content{
      padding: .75em 0 0;
    }
    &__title{
      font-size: 1.0625em;
      line-height: 135%;
    }
  }

  .test{
    padding: 0;
    &__inner{
      background-image: url(../img/test-bg2.webp);
      padding: 2.25em .8125em 0;
      grid-template-columns: 100%;
      gap: 0;
    }
    &__content{
      gap: .75em;
      text-align: center;
    }
    &__title{
      font-size: 1.3125em;
      line-height: 133%;
    }
    &__nav{
      margin-top: .5em;
      justify-content: center;
    }
    &__btn{
      padding: 0 4.5em;
      height: 3.125em;
    }
    &__img{
      order: 2;
      width: 17em;
      margin: -1em auto 0;
      transform: translate(1em,2em);
      img{
        width: 100%;
        height: auto;
        position: static;
      }
    }
  }

  .pagination{
    margin-top: 2.375em;
    &__nums{
      display: none;
    }
    &__btn{
      margin: 0 .25em;
      span{
        display: none;
      }
      svg{
        margin: 0;
      }
      &:hover{
        path{
          fill: #fff;
        }
      }
    }
  }
}


@media screen and (max-width: 576px){
  .blog{
    &__grid{
      grid-template-columns: 100%;
      gap: 1.25em;
    }
  }
}
