@import "../mixins";
.header{
  padding: 1.3125em 0;
  position: relative;
  z-index: 3;
  .container{
    max-width: 1440px;
  }
  &__inner{
    display: flex;
    align-items: center;
  }
  &__left{
    display: flex;
    align-items: center;
    flex-grow: 1;
  }
  &__logo{
    display: block;
    width: 8em;
    flex-shrink: 0;
    margin-right: 4.5em;
    img{
      width: 100%;
      display: block;
    }
  }
  &__search{
    width: .9375em;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
      transform: translateY(0%);
    }
  }
}

.burger,
.header__search{
  flex-shrink: 0;
  svg{
    width: 100%;
    height: auto;
  }
  &:hover{
    path{
      stroke: $primary;
    }
  }
}

.burger{
  width: 1.125em;
  margin-right: 3.8125em;
}

.header-contacts{
  margin-left: auto;
  display: flex;
  align-items: center;
  &__social{
    width: 2.3125em;
    height: 2.3125em;
    border-radius: 50%;
    background: #FFFFFF;
    box-shadow: 0 .25em .5em rgba(0, 0, 0, 0.02);
    @include center;
    margin-right: .375em;
    flex-shrink: 0;
    img{
      max-width: 1em;
    }
    &:hover{
      box-shadow: 0 .25em .5em rgba(0, 0, 0, 0.1);
    }
  }
  &__phone{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    margin-left: .375em;
    &-link{
      font-size: 1.0625em;
      line-height: 120%;
      font-weight: 500;
    }
    &-btn{
      font-weight: 500;
      font-size: .8125em;
      line-height: 154%;
      color: $primary;
      border-bottom: 1px dashed rgba($primary,.3);
      &:hover{
        border-color: transparent;
      }
    }
  }
}

.header-action{
  display: flex;
  align-items: center;
  flex-shrink: 0;
  margin-left: 2.625em;
  &__btn{
    margin-left: 1em;
  }
  .m-btn-outline{
    padding-left: 1em;
    padding-right: 1.3125em;
  }
}

.lang{
  margin-left: 2em;
  position: relative;

  &__show{
    position: relative;
    z-index: 3;
    display: flex;
    align-items: center;
    height: 2em;
    &:hover{
      cursor: pointer;
      path{
        opacity: 1;
      }
    }
    svg{
      width: .5em;
      height: auto;
      margin-left: .625em;
    }
    &._active{
      &~.lang__hidden{
        opacity: 1;
        pointer-events: unset;
      }
    }
  }
  &__icon{
    flex-shrink: 0;
    margin-right: .375em;
    width: .9375em;
    img{
      width: 100%;
    }
  }
  &__title{
    letter-spacing: 0.15em;
    text-transform: uppercase;
    font-weight: 700;
  }
  &__hidden{
    position: absolute;
    top: 0;
    left: -.625em;
    width: calc(100% + 1.25em);
    padding-top: 2em;
    background-color: #fff;
    box-shadow: 0 .25em .625em rgba(0, 0, 0, 0.09);
    border-radius: .4375em;
    overflow: hidden;
    opacity: 0;
    pointer-events: none;
  }
  &__link{
    height: 1.6875em;
    display: flex;
    align-items: center;
    padding-left: 1.9375em;
    span{
      font-size: .75em;
    }
    &:hover,
    &._active{
      background-color: $primary;
      color: #fff;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    padding: 1em 0 1.125em;
    .container{
      padding-left: .625em;
      padding-right: .625em;
    }
    &__inner{
      padding-bottom: 3.125em;
      position: relative;
    }
    &__logo{
      //transform: translateX(1.5625em);
      width: 6.625em;
    }
    &__left{
      justify-content: space-between;
      &:after{
        content: '';
        width: 1.125em;
      }
    }
    &__search{
      display: none;
    }
  }

  .header-contacts{
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    align-items: center;
    &__phone{
      margin-left: .25em;
      white-space: nowrap;
      &-btn{
        display: none;
      }
    }
  }

  .header-action{
    margin: 0;
    &__btn{
      margin: 0;
    }
    &__reg{
      display: none;
    }
    &__login{
      position: absolute;
      right: 0;
      top: .4375em;
      z-index: 2;
      background-color: #fff;
      width: 2.3125em;
      height: 2.3125em;
      border-radius: 50%;
      box-shadow: 0 .25em .5em rgba(0, 0, 0, 0.02);
      padding: 0;
      border: none;
      background-image: url(../img/login2.svg);
      @include bgImgSize(1em);
      svg,span{
        display: none;
      }
      &:hover{
        box-shadow: 0 .25em .5em rgba(0, 0, 0, 0.1);
      }
    }
  }

  .lang{
    display: none;
  }
}
